
    import {defineComponent, onMounted, ref} from 'vue'
    import Canvas from './index'

    export default defineComponent({
        name: "Index",
        setup() {
            let colors = ref("red");
            let currentType = ref("free");
            let colorChange = () => {
                canvas.colorChange(colors.value);
            }
            let canvas: any = null;
            onMounted(() => {
                canvas = new Canvas('drawBox', 'canvasBox', {
                    color: "red",
                    strokeWidth: 3
                });
            });
            let selectDraw = () => {
                currentType.value = "selectable";
                canvas.selectDraw();
            };
            let lineDraw = () => {
                currentType.value = "line";
                canvas.lineDraw();
            };
            let rectDraw = () => {
                currentType.value = "rect";
                canvas.rectDraw();
            };
            let triangleDraw = () => {
                currentType.value = "triangle";
                canvas.triangleDraw();
            };
            let circleDraw = () => {
                currentType.value = "circle";
                canvas.circleDraw();
            };
            let freeDraw = () => {
                currentType.value = "free";
                canvas.freeDraw();
            };
            let clear = () => {
                canvas.clear();
            };
            let textDraw = () => {
                currentType.value = "text";
                canvas.textDraw();
            };
            let unselectDraw = () => {
                currentType.value = "unselectable";
                canvas.unselectDraw();
            };
            let del = () => {
                canvas.del();
            };
            let undoDraw = () => {
                canvas.undoDraw();
            };
            let exportCanvas = () => {
                canvas.exportCanvas();
            };
            return {
                selectDraw,
                lineDraw,
                rectDraw,
                triangleDraw,
                circleDraw,
                freeDraw,
                clear,
                textDraw,
                unselectDraw,
                del,
                undoDraw,
                exportCanvas,
                colors,
                colorChange,
                currentType
            }
        }
    })
